body { 
	background-color: #E0E0E0;
	font-family: 'Roboto', sans-serif;
	height: 100vh;
 
}

.container-app{
	min-height: 85vh;
}

.title-app{	
	font-size: 40px;
	margin-Left: auto;
	margin-Right :auto;
}

.card-clock {
	animation: appear 500ms ease-out forwards;
	background: white;
	border-radius: 10px;
	box-shadow: 0px 0px 25px 1px rgba(50, 50, 50, 0.1);	
	height: 196px;
	min-width: 154px;
	overflow: hidden;
	position: relative;

}
.card-clock i {
	cursor: pointer;
}

.card-subtitle{
	font-size: 19px;
}

.card-text{
	font-size: 25px;
}

.spinner {

	animation: spin 1s ease infinite;
	border: 4px solid rgba(0, 0, 0, 0.1);	
	border-radius: 50%;
	border-left-color: #736F6F;
	display: flex;
    flex-direction: column;
	align-items: center;	
	margin-top: 35px;
    margin-right: auto;
	margin-left: auto;
	height: 36px;
	width: 36px;
	
}

@keyframes spin {
	0% {
	  transform: rotate(0deg);
	}
  
	100% {
	  transform: rotate(360deg);
	}
  }

.footer{
	bottom: 0;
	height: 15vh;
	
}

@media (max-width: 500px) {
    .footer{
        height: auto;
    }
}
